.form{
    margin-top: 1rem;
    /* border: 2px solid black; */
    /* text-align: center; */
    display: flex;
    justify-content: center;
    gap: 1rem;
}

input{
    padding: 1% 3%;
    outline: none;
    border: none;
    width: 80%;
    background-color: rgb(109, 0, 131);
    color: rgb(255, 255, 255);
    font-size: 2rem;
    
}

button{
    border: none;
    color: rgb(255, 255, 255);
    outline: none;
    padding: 1% 1%;
    background-color: blueviolet;
    width: 9%;
    font-size: 1.2rem;
    font-weight: 700;
    cursor: pointer;
    transition: width 0.3s;


}

button:hover{
    background-color: rgb(156, 51, 255);
    width:10% ;
}

::placeholder{
    color: blueviolet;
    padding: 1% 0.4%;
}