
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap");
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Poppins;
}
.everyitemlist{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0.3rem 0.3rem;
    width: 90%;
    text-align: center;
    
    
    
    background-color: rgb(186, 120, 248);
    margin: auto;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    position: relative;
}

.number{
    
    padding: 1% 1%;
    
    background-color: rgb(100, 2, 56);
    position: absolute;
    left: 1rem;
    color: rgb(255, 255, 255);
    font-weight: 600;
}


.titleitem{
    
    padding: 1% 1%;
    margin-left: 3rem;
    font-size: 1.5rem;
    font-weight: 900;
    color: rgb(56, 0, 160);
    background-color: rgb(207, 166, 245);
    
    
    

    
}


.editbtn{
   background-color: rgb(134, 1, 128);
    padding: 1% 1%;
    position: absolute;
    right: 6rem;
    color: wheat;
    font-weight: 700;
    cursor: pointer;

}


.del{
    background-color: rgb(196, 3, 186);
    padding: 1% 1%;
    position: absolute;
    right: 1rem;
    color: wheat;
    font-weight: 700;
    cursor: pointer;
   

}



.title{
    text-align: center;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    
}

.total{
    /* border: 2px solid black; */
    width: 60%;
    margin: auto;
    margin-top: 4rem;
}

.itemsList{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-content: center;
    margin: auto;
    /* border: 2px solid black; */
    /* width: 20%; */
}

.inputedit{
    padding: 1% 3%;
    outline: none;
    border: none;
    background-color: rgb(19, 13, 20);
    color: rgb(255, 255, 255);
    font-size: 1rem;
    width:80%;
    
}

.insteadEdit{
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 90%;
}

.okbtn{
    background-color: rgb(134, 1, 128);
    padding: 1% 1%;
    position: absolute;
    right:5rem;
    color: wheat;
    font-weight: 700;
    cursor: pointer;
}

.cancelbtn{
    background-color: rgb(88, 1, 84);
    padding: 1% 1%;
    position: absolute;
    right: 10rem;
    color: wheat;
    font-weight: 700;
    cursor: pointer;
    
}